import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Spin, Modal } from 'antd';
import { getQueryParams } from 'utils/urlUtil';
import { makeid } from 'utils/functionUtil';
import {
  getStudentHasGradeSummary,
  getStudentGradeSummary,
  exportPdfStudentGradeSummary,
  printPdfStudentGradeSummary,
  exportExcelStudentGradeSummary,
} from 'services/classCreditEnrollment';

import FormInfoStudentForTranscript from './FormInfoStudentForTranscript';
import TableListStudent from './TableListStudent';
import ModalStudentForTranscript from './ModalStudentForTranscript';
import './style.scss';

class ListStudentForTranscriptPage extends Component {
  constructor(props) {
    super(props);
    this.refForm = React.createRef();
    this.refFormRaw = React.createRef();
    this.state = {
      loading: false,
      loadingModal: false,
      reload: 0,
      dataStudents: [],
      dataListSubject: [],
      studentFullName: '',
      studentId: '',
      showModal: false,
      rowKey: 'autoKey',
      selectedRows: [],
    };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  getStudentGradeSummary = async (studentId) => {
    const { loading } = this.state;
    if (loading) {
      return;
    }
    try {
      const paramSearch = getQueryParams();
      if (
        !!paramSearch.fromSemesterNo &&
        !!paramSearch.toSemesterNo
      ) {
        this.setState({
          loading: true,
        });
        paramSearch.studentId = studentId;
        const response = await getStudentGradeSummary(paramSearch);
        const tempData = response.data.gradeDetails.map(item => {
          item.autoKey = `${makeid(8)}_${item.studentId}`;
          return item;
        });
        this.setState({
          studentId,
          dataListSubject: [...tempData],
          studentFullName: response.data.studentFullName,
          showModal: true,
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  handleChangeQueryParams = async () => {
    const { loading } = this.state;
    if (loading) {
      return;
    }
    try {
      const paramSearch = getQueryParams();
      if (
        !!paramSearch.trainTypeId &&
        !!paramSearch.facultyId &&
        !!paramSearch.courseId &&
        !!paramSearch.fromSemesterNo &&
        !!paramSearch.toSemesterNo
      ) {
        this.setState({
          loading: true,
        });
        const response = await getStudentHasGradeSummary(paramSearch);
        const tempData = response.data.items.map(item => {
          item.autoKey = `${makeid(8)}_${item.studentId}`;
          return item;
        });
        this.setState({
          dataStudents: [...tempData],
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  formatStudentIds = () => {
    const studentIds = [];
    const { selectedRows } = this.state;
    selectedRows.forEach(item => {
      studentIds.push(item.studentId);
    });
    return studentIds;
  }

  formatParamsSearch = (studentIds) => {
    const paramSearch = getQueryParams();
    if (studentIds) {
      paramSearch.studentIds = studentIds;
    } else {
      paramSearch.studentIds = [this.state.studentId];
    }
    return paramSearch;
  }

  handleExportPDFList = () => {
    const studentIds = this.formatStudentIds();
    this.handleExportPDF(studentIds);
  }

  handlePrintPDFList = () => {
    const studentIds = this.formatStudentIds();
    this.handlePrintPDF(studentIds);
  }


  handleExportPDF = (studentIds) => {
    try {
      if (studentIds) {
        this.setState({
          loading: true,
        });
      } else {
        this.setState({
          loadingModal: true,
        });
      }
      const paramSearch = this.formatParamsSearch(studentIds)
      exportPdfStudentGradeSummary(paramSearch);

    } catch (error) {
      console.log(error);
    } finally {
      if (studentIds) {
        this.setState({
          loading: false,
        });
      } else {
        this.setState({
          loadingModal: false,
        });
      }
    }
  }

  handlePrintPDF = (studentIds) => {
    try {
      this.setState({
        loading: true,
      });
      const paramSearch = this.formatParamsSearch(studentIds)
      printPdfStudentGradeSummary(paramSearch);

    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  handleExportExcel = async () => {
    try {
      this.setState({
        loadingModal: true,
      });
      const paramSearch = getQueryParams();
      paramSearch.studentId = this.state.studentId;
      await exportExcelStudentGradeSummary(paramSearch);
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loadingModal: false,
      });
    }
  }

  hideModal = () => {
    this.setState({
      showModal: false,
      dataListSubject: [],
      studentFullName: '',
      studentId: '',
    });
  };

  handleSelectedRows = (selectedRows) => {
    this.setState({
      selectedRows,
    });
  }

  render() {
    const {
      dataStudents,
      dataListSubject,
      studentFullName,
      studentId,
      reload,
      rowKey,
      loading,
      loadingModal,
      showModal,
      selectedRows
    } = this.state;
    const disabledExport = selectedRows.length === 0;
    return (
      <Fragment>
        <Spin spinning={loading}>
          <Card title="In bảng điểm sinh viên">
            <Row>
              <Col span={24}>
                <FormInfoStudentForTranscript
                  disabledExport={disabledExport}
                  onClickExport={this.handleExportPDFList}
                  onClickPrint={this.handlePrintPDFList}
                />
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <TableListStudent
                  reload={reload}
                  data={dataStudents}
                  ref={this.refForm}
                  rowKey={rowKey}
                  wrappedComponentRef={(form) => {
                    this.refFormRaw = form;
                  }}
                  getStudentGradeSummary={this.getStudentGradeSummary}
                  handleSelectedRows={this.handleSelectedRows}
                />
              </Col>
            </Row>
          </Card>
        </Spin>
        <Modal
          title={`${studentFullName} - Danh sách môn học`}
          visible={showModal}
          onOk={this.hideModal}
          onCancel={this.hideModal}
          footer={null}
          width="70%"
          height="60%"
        >
          <ModalStudentForTranscript
            dataListSubject={dataListSubject}
            studentId={studentId}
            onExportExcel={this.handleExportExcel}
            onExportPDF={this.handleExportPDF}
            loading={loadingModal}
          />
        </Modal>
        <div id="printerDiv" />
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListStudentForTranscriptPage />
  </Page>
);



